<template>
    <b-row>
        <b-col cols="5">
            <basic-row-add-link class="w-50"
                :ddItems="getddRequirementsGroupByTablet"
                buttonText="Requirement"
                @addItem="addRequirement"
                @linkItem="linkRequirement" />
        </b-col>
        <b-col cols="1">
            <b-form-select class="m-2" v-model="selectedLayer" :options="layers"></b-form-select>
        </b-col>
        <b-col cols="1"><b-dropdown :text="selectedLayer + ' (' + getScopedElementIds.length + ')'" class="m-2" variant="outline-dark">
            <b-dropdown-item-button @click="resetScopedElements()"><i>Auswahl aufheben</i></b-dropdown-item-button>
            <b-dropdown-divider></b-dropdown-divider>
            <b-dropdown-item-button v-for="elem in ddLayerElements" :value="elem.value" :key="elem.value"
                :active="getScopedElementIds.indexOf(elem.value) != -1"
                @click="addScopeElementId(elem.value)">
                {{ elem.text }}
            </b-dropdown-item-button>
        </b-dropdown></b-col>
        <b-col cols="1"><b-form-checkbox v-model="onlyScopedInternal" v-show="getScopedElementIds.length > 0">
            nur diese Auswahl (Start-Ende-Element)
        </b-form-checkbox>
        <b-form-checkbox v-model="withElementInternal" v-show="getScopedElementIds.length > 0">
            Element-intern
        </b-form-checkbox></b-col>
        <!--<b-col cols="1"><b-dropdown :text="'Tablet (' + selectedTablet.length + ')'" class="m-2" variant="outline-dark">
            <b-dropdown-item-button @click="setActiveTablet('none')"><i>Auswahl aufheben</i></b-dropdown-item-button>
            <b-dropdown-divider></b-dropdown-divider>
            <b-dropdown-group v-for="tab in ddTablet" :header="tab.label" :key="tab.label">
                <b-dropdown-item-button v-for="opt in tab.options" :value="opt" :key="opt"
                    :active="isActiveTablet(opt)"
                    @click="setActiveTablet(opt)">
                    {{ opt }}
                </b-dropdown-item-button>
            </b-dropdown-group>
        </b-dropdown></b-col>-->
        <b-col cols="4" class="text-right align-text-bottom">{{ "zeigt " + this.getScopedRequirements.length + " von " + getAllRequirements.length}}</b-col>
    </b-row>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import BasicRowAddLink from './BasicRowAddLink.vue'

export default {

    components: {
        BasicRowAddLink
    },

    data() {
        return {
            selectedIds: [],
            selectedTablet: [],
            onlySelectedProjects: false,
            withInternal: true
        }
    },

    computed: {
        selectedLayer: {
            get() {
                return this.getSelectedLayer
            },
            set(newValue) {
                this.setSelectedLayer(newValue)
            }
        },

        onlyScopedInternal: {
            get() {
                return this.getScopedArgumentOnlyScopedInternal
            },
            set(newValue) {
                this.setScopedArgumentOnlyScopedInternal(newValue)
            }
        },

        withElementInternal: {
            get() {
                return this.getScopedArgumentWithElementInternal
            },
            set(newValue) {
                this.setScopedArgumentWithElementInternal(newValue)
            }
        },

        ddLayerElements() {
            var layerElements = this.getddElementsGroupByType.find(e => e.label == this.selectedLayer)
            if(layerElements != undefined) {
                return layerElements.options
            }
            return []
        },

        ddTablet() {
            return this.$tabletComponents;
        },

        ...mapGetters([
            'getddRequirementsGroupByTablet',
            'getScopedElementIds',
            'layers',
            'getddElementsGroupByType',
            'getAllRequirements',
            'getSelectedLayer',
            'getScopedArgumentOnlyScopedInternal',
            'getScopedArgumentWithElementInternal',
            'getLastAddedRelation',
            'getLastAddedElement',
            'getElementById',
            'getScopedRequirements'
        ])
    },

    methods: {
        isActiveTablet(option) {
            return (this.selectedTablet.indexOf(option) == -1 ? false : true);
        },

        setActiveTablet(selectedOption) {
            if(selectedOption == 'none') {
                this.selectedTablet = [];
            } else if(this.selectedTablet.indexOf(selectedOption) == -1) {
                this.selectedTablet.push(selectedOption);
            } else {
                this.selectedTablet = this.selectedTablet.filter(i => i != selectedOption);
            };
            this.emitTablet();
        },

        addRequirement(elementName) {
            this.addElement({
                name: elementName,
                type: 'Requirement'
            })

            var newRequirement = this.getLastAddedElement
            
            if(this.getScopedElementIds.length > 0) {
                this.changeElementProp({
                    id: newRequirement.id,
                    prop: 'requirementSource',
                    newValue: this.getScopedElementIds[0]
                })

                this.linkRequirement(newRequirement.id)
                var newRealization = this.getLastAddedRelation
                this.setSelectedRelationById(newRealization.id)
            } else {
                this.setSelectedRelationById("")
            }
            this.setSelectedRequirementById(newRequirement.id)
            this.setShowRelationModal(true)
        },

        linkRequirement(elementId) {
            var relationId = ""
            if(this.getScopedElementIds.length > 0) {
                var startRelationId = this.getScopedElementIds[0]
                if(this.getScopedElementIds[1] != undefined) {
                    var endRelationId = this.getScopedElementIds[1]
                    var relationLayer = this.getElementById(endRelationId).type
                } else {
                    var endRelationId = ""
                    var relationLayer = ""
                }

                this.addRelation({
                    startId: startRelationId,
                    endId: endRelationId,
                    layer: relationLayer,
                    requirementId: elementId
                })
                relationId = this.getLastAddedRelation.id
            }
            this.setSelectedRelationById(relationId)
            this.setSelectedRequirementById(elementId)
            this.setShowRelationModal(true)
        },

        ...mapMutations([
            'resetScopedElements',
            'addScopeElementId',
            'setSelectedLayer',
            'setScopedArgumentWithElementInternal',
            'setScopedArgumentOnlyScopedInternal',
            'setSelectedRelationById',
            'setSelectedRequirementById',
            'setShowRelationModal',
            'addRelation',
            'addElement',
            'changeElementProp'
        ])
    }
    
}
</script>

<style scoped>

</style>


