export default {
    getters: {
        commitmentTypes(state) {
            return state.commitment
        },

        requirementTypes(state) {
            return state.requirementTypes
        },

        statusTypes(state) {
            return state.relationStatus
        },

        relationTypes(state) {
            return state.relationTypes
        },

        layers(state) {
            return state.viewLayers.map(l => l.name)
        },

        tabletTypes(state) {
            return state.tabletComponents
        },

        tabletOptions(state) {
            var options = []
            state.tabletComponents.forEach((lab) => {
                lab.options.forEach((opt) => {
                    options.push(opt);
                })
            })
            return options;
        }
    },

    mutations: {

    },

    state: () => ({
        tabletComponents: [{
                "label": "Service Utility",
                "options": ["Service Utility"]
            },
            {
                "label": "Service Warranty",
                "options": ["Service Warranty", "Verfgbarkeit", "Sicherheit", "Kapazitt"]
            },
            {
                "label": "Organisation",
                "options": ["Organisation", "Kapazitten", "Fhigkeiten"]
            },
            {
                "label": "Prozesse",
                "options": ["Prozesse"]
            },
            {
                "label": "Finanzen",
                "options": ["Finanzen"]
            },
            {
                "label": "Reporting",
                "options": ["Reporting"]
            },
            {
                "label": "Management Systeme",
                "options": ["Management Systeme"]
            },
            {
                "label": "Technik",
                "options": ["Technik", "Informationssicherheit"]
            },
            {
                "label": "Inter-Service-Beziehungen",
                "options": ["Inter-Service-Beziehungen"]
            }
        ],

        commitment: [
            "Vorgabe",
            "Anforderung",
            "Annahme"
        ],

        requirementTypes: [
            "Funktional",
            "Nicht-Funktional"
        ],

        relationStatus: [
            "aufgenommen",
            "freigegeben",
            "realisiert",
            "zurckgestellt",
            "ausgeschlossen"
        ],

        relationTypes: {
            "norelation": {
                "name": "no relation",
                "fill": "#dddddd"
            },
            "consume": {
                "name": "consume",
                "fill": "#cd5c5c"
            },
            "dependency": {
                "name": "dependency",
                "fill": "#5c9acd"
            },
            "bidirection": {
                "name": "bi-direction",
                "fill": "#cdb35c"
            },
            "selection": {
                "name": "selection",
                "fill": "#eeee00"
            }
        },

        viewLayers: [{ // sort: high to low
            "name": "Projekt",
        }, {
            "name": "Funktionalitt",
        }, {
            "name": "Release",
        }, {
            "name": "Solution",
        }, {
            "name": "Serviceinstanz",
        }, {
            "name": "Applikation",
        }, {
            "name": "Netzwerk",
        }]
    }),

}


